import { db } from './firebase-config'
import "firebase/firestore";
import "firebase/auth";
import { DataGrid } from '@material-ui/data-grid';
import { useEffect, useState } from "react";
import { Button } from '@material-ui/core';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';

const App = () => {
  const [rowsVal, setRowsVal] = useState([]);
  const [isOpen, setIsOpen] = useState(false);
  const [values, setValues] = useState({
    id: null,
    name: "",
    case: "",
    comments: ""
  });
  const columns = [
    { field: 'id', headerName: 'ID', width: 70 },
    { field: 'name', headerName: 'Name', width: 130 },
    { field: 'case', headerName: 'Case', width: 130 },
    { field: 'comments', headerName: 'Comments', width: 160 },
  ];

  useEffect(() => {
    db.collection("users")
      .orderBy("id")
      .onSnapshot(querySnapshot => {
        const data = querySnapshot.docs.map(doc => doc.data());
        setRowsVal(data);
      });
  }, []);

  const handleCreateRecord = () => {
    db.collection("users").add({
      id: values.id,
      name: values.name,
      case: values.case,
      comments: values.comments,
    })
      .then(() => {
        setIsOpen(false);
      })
      .catch((error) => {
        console.error("Error adding document: ", error);
      });
  };

  const handleOpen = () => {
    setIsOpen(true);
  };

  const handleClose = () => {
    setIsOpen(false);
  };

  const handleChange = name => event => {
    setValues({ ...values, [name]: event.target.value });
  }
  
  return (
    <div style={{ height: 500, width: '100%' }}>
      <DataGrid rows={rowsVal} columns={columns} pageSize={10} checkboxSelection />

      <Button variant="contained" color="primary" onClick={handleOpen}>
        Create
      </Button>

      <Dialog open={isOpen} onClose={handleClose} aria-labelledby="form-dialog-title">
        <DialogTitle id="form-dialog-title">Add Record</DialogTitle>
        <DialogContent>
          <div
            style={{ display: 'flex', flexDirection: 'column' }}>
            <TextField
              id="id"
              label="ID"
              value={values.id}
              onInput={handleChange('id')}
            />
            <TextField
              id="Name"
              label="Name"
              value={values.name}
              onInput={handleChange('name')}
            />
            <TextField
              id="Case"
              label="Case"
              value={values.case}
              onInput={handleChange('case')}
            />
            <TextField
              id="Comments"
              label="Comments"
              value={values.comments}
              onInput={handleChange('comments')}
            />
          </div>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Cancel
          </Button>
          <Button onClick={handleCreateRecord} color="primary">
            Add
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );

}

export default App;
